<template>
  <div>
    <div class="chart-title">xxxxxx图表</div>
    <v-chart
      :force-fit="true"
      height="506"
      :padding="['auto',20,'auto',9]"
      renderer="svg"
      :scale="scale"
      ref="chart"
    >
      <v-tooltip :shared="true" :htmlContent="htmlContent" />
      <v-legend position="top-center" :offsetY="-13"></v-legend>
      <v-axis data-key="value" :label="null" :line="null" :grid="null" />
      <v-axis data-key="x" :line="null" :label="null" :tickLine="null" />
      <!-- :custom="true"
        :items="legendItems"
        :itemWidth="170"
        :itemGap="130"
        :itemMarginBottom="20"
        :textStyle="{fill:'#333333',lineHeight:20,fontSize:16}"
        :onClick="legendOnClick"
      :onHover="legendOnHover"-->
      <!--Y轴箭头-->
      <v-guide
        type="html"
        :position="['start','end']"
        html="<svg width='18' height='20'><path d='M9 0 L0 20  L18 20  Z' stroke='rgb(141,201,255)' fill='rgb(141,201,255)'/></svg>"
        :top="true"
        :offsetY="8"
      ></v-guide>
      <!--层叠柱状图-->
      <v-view :data="chartData._bar" :scale="[{dataKey:'value',sync: true,nice:true}]">
        <v-interval
          position="x*value"
          :color="color"
          :tooltip="tooltip"
          :adjust="adjust"
          :size="80"
        />
        <!--Y轴-->
        <v-guide
          type="line"
          :top="true"
          :start="['start','max']"
          :end="['start',0]"
          :lineStyle="linestyle"
          :text="{}"
        ></v-guide>
        <!--X轴-->
        <v-guide
          type="line"
          :top="true"
          :start="['start',0]"
          :end="['end',0]"
          :lineStyle="linestyle"
        ></v-guide>
        <!--X轴箭头-->
        <v-guide
          type="html"
          :position="['end',0]"
          html="<svg width='20' height='18'><path d='M0 0 L 20 9 L 0 18  Z' stroke='rgb(141,201,255)' fill='rgb(141,201,255)'/></svg>"
          :top="true"
          :offsetX="9"
          :offsetY="-1"
        ></v-guide>
      </v-view>
      <!--折线/点图-->
      <v-view :data="chartData._line" :scale="[{dataKey:'value',sync: true}]">
        <v-line position="x*value" :size="4" color="#ff1fa2"></v-line>
        <v-point position="x*value" :size="8" color="#ffffff"></v-point>
      </v-view>
    </v-chart>
  </div>
</template>

<script>
import Utils from "@antv/util/lib";
const DataSet = require("@antv/data-set");
let filterData = [];
let rows = [];
const data = [
  {
    x: "2020/5/1",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: 51,
    辅助单元能耗: -72,
    系列单位综合能耗: 140
  },
  {
    x: "2020/5/2",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: 51,
    辅助单元能耗: -72,
    系列单位综合能耗: 80
  },
  {
    x: "2020/5/3",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: -51,
    辅助单元能耗: -72,
    系列单位综合能耗: 120
  },
  {
    x: "2020/5/4",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: 51,
    辅助单元能耗: -72,
    系列单位综合能耗: 90
  },
  {
    x: "2020/5/5",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: -51,
    辅助单元能耗: -72,
    系列单位综合能耗: 80
  },
  {
    x: "2020/5/6",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: 51,
    辅助单元能耗: -72,
    系列单位综合能耗: 90
  },
  {
    x: "2020/5/7",
    脱硫单元能耗: 130,
    脱水单元能耗: 130,
    硫磺回收单元能耗: 51,
    尾气处理单元能耗: 51,
    酸水汽提单元能耗: 51,
    辅助单元能耗: -72,
    系列单位综合能耗: 80
  }
];

export default {
  computed: {
    chartData() {
      let dv = new DataSet.View().source(data);
      dv.transform({
        type: "fold",
        fields: [
          "脱硫单元能耗",
          "脱水单元能耗",
          "硫磺回收单元能耗",
          "尾气处理单元能耗",
          "酸水汽提单元能耗",
          "辅助单元能耗",
          "系列单位综合能耗"
        ],
        key: "key",
        value: "value",
        retains: ["x"]
      });
      dv.transform({
        type: "map",
        callback(row) {
          if (row.key == "系列单位综合能耗") row.type = "line";
          else row.type = "bar";
          return row;
        }
      });
      dv.transform({
        type: "partition",
        groupBy: ["type"] // 以type字段进行分组
      });
      rows = dv.rows;
      return dv.rows;
    }
  },
  data() {
    function markerCallback(x, y, r) {
      return [
        ["M", x - 35, y - 10],
        ["L", x + 5, y - 10],
        ["L", x + 5, y + 10],
        ["L", x - 35, y + 10],
        ["Z"]
      ];
    }
    return {
      linestyle: {
        stroke: "rgba(141,201,255)",
        lineWidth: 2,
        lineDash: null
      },
      htmlContent: function(title, items) {
        //-1 样式
        let tooltipsttyle = `position: absolute;
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: 3px;
        color: rgb(87, 87, 87);
        font-size: 20px;
        line-height: 26px;
        padding: 10px 10px 6px 10px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)`;
        let listStyle = `margin: 0;
        padding: 10px 0;
        list-style-type: none;text-align:left;`;
        let valurStyle = `margin-left: 30px;
        display: inline;
        float: right;`;

        //-2 构造tooltip
        let li = "";
        for (let i = 0; i < items.length; i++) {
          let item = items[i];
          if (item.name == "value") item.name = "系列单位综合能耗";
          li += `<li data-index={index}>
          <span style="background-color:${item.color};width:8px;height:8px;border-radius:50%;display:inline-block;margin-right:8px;">
          </span>${item.name}<span class="g2-tooltip-value" style="${valurStyle}">${item.value}</span>
          </li>`;
        }
        let dom = `<div class="g2-tooltip" style="${tooltipsttyle}">
        <div class="g2-tooltip-title" style="margin-bottom: 4px;">${title}</div>
        <ul class="g2-tooltip-list" style="${listStyle}">
        ${li}
        </ul>
        </div>`;
        return dom;
      },
      legendItems: [
        // {
        //   value: "line",
        //   marker: {
        //     symbol: markerCallback,
        //     fill: "#92b157",
        //   },
        // },
        {
          value: "脱硫单元能耗",
          marker: {
            symbol: markerCallback,
            fill: "#92b157"
          }
        },
        {
          value: "脱水单元能耗",
          marker: {
            symbol: markerCallback,
            fill: "#88628b"
          }
        },
        {
          value: "硫磺回收单元能耗",
          marker: {
            symbol: markerCallback,
            fill: "#7fd0bd"
          }
        },
        {
          value: "尾气处理单元能耗",
          marker: {
            symbol: markerCallback,
            fill: "#6b9e92"
          }
        },
        {
          value: "酸水汽提单元能耗",
          marker: {
            symbol: markerCallback,
            fill: "#a4a79d"
          }
        },
        {
          value: "辅助单元能耗",
          marker: {
            symbol: markerCallback,
            fill: "#e2e767"
          }
        }
      ],
      legendOnClick: (ev, chart) => {
        let field = "key";
        let views = chart.get("views");
        let view = views[0];
        let item = ev.item;
        let checked = ev.checked;
        let clickedValue = item.dataValue; // import: 需要取该图例项原始的数值
        let filterVals = view.getFilteredOutValues(field);
        if (checked) {
          // Util.Array.remove(filterVals, clickedValue);
          // if (self._isFieldInView(field, clickedValue, chart)) {
          //   chart.filter(field, function(field) {
          //     return isSingleSelected
          //       ? field === clickedValue
          //       : !Util.inArray(filterVals, field);
          //   });
          // }
          // Util.each(views, function(view) {
          //   if (self._isFieldInView(field, clickedValue, view)) {
          //     view.filter(field, function(field) {
          //       return isSingleSelected
          //         ? field === clickedValue
          //         : !Util.inArray(filterVals, field);
          //     });
          //   }
          // });
        } else {
          filterVals.push(clickedValue);
          chart.filter(field, function(field) {
            return !Utils.contains(filterVals, field);
          });

          Utils.each(views, function(view) {
            view.filter(field, function(field) {
              return !Utils.contains(filterVals, field);
            });
          });
        }

        chart.set("keepLegend", true); // 图例不重新渲染

        chart.set("keepPadding", true); // 边框不重新计算

        chart.repaint();
        chart.set("keepPadding", false);
        chart.set("keepLegend", false);
      },
      legendOnHover: (ev, chart) => {
        const item = ev.item;
        const value = item.value;
        const checked = ev.checked;
        const geoms = chart.getAllGeoms();
        console.log(geoms, geoms[0].getShapes());
      },
      test() {
        alert();
      },
      scale: [
        {
          dataKey: "x",
          type: "cat"
        },
        {
          dataKey: "value",
          sync: true
        }
      ],
      intervalLabel: [
        "value",
        {
          textStyle: {
            fill: "#fff",
            fontSize: 16
          },
          offset: 15,
          type: "interval",
          formatter: (val, item, i) => {
            return `${val}t标煤\n50%`;
          },
          position: "middle"
        }
      ],

      style: {
        shadowColor: "rgba(153, 153, 153, 0.9)",
        shadowOffsetX: 10,
        shadowOffsetY: -3,
        fillOpacity: 1,
        shadowBlur: 35
      },
      line: {
        lineWidth: 2,
        stroke: "rgb(141,201,255)",
        fill: "rgb(141,201,255)"

        // lineJoin: "round"
      },
      line2: {
        lineWidth: 2,
        stroke: "rgb(141,201,255)",
        fill: "rgb(141,201,255)"
      },

      height: 343,

      color: [
        "key",
        ["#92b157", "#88628b", "#7fd0bd", "#6b9e92", "#a4a79d", "#e2e767"]
      ],
      padding: [20, 160, 80, 60],
      label: {
        textStyle: {
          fill: "#333333",
          fontSize: 16,
          fontWeight: 550
        },
        offsetY: 5,
        autoRotate: false
      },
      tooltip: [
        "key*value",
        (key, value) => {
          return {
            name: key,
            value: value
          };
        }
      ],
      adjust: [
        {
          type: "dodge",
          dodgeBy: "type" // 按照 type 字段进行分组
          //  marginRatio: 0 // 分组中各个柱子之间不留空隙
        },
        {
          type: "stack"
        }
      ]
    };
  },
  methods: {}
};
</script>
<style>
