<template>
  <el-table-column
    align="center"
    :width="col.width"
    :prop="col.prop"
    :fixed="col.fixed"
    :min-width="minWidth"
    show-overflow-tooltip
  >
    <template slot="header">
      <el-checkbox v-if="hasCheckbox" :checked="col.checked" @change="checkChange">{{col.label}}</el-checkbox>
      <span class="column-text" style v-else>{{col.label}}</span>
    </template>

    <template v-if="col&&col.children">
      <custom-column
        v-for="(item,index) in col.children"
        :key="index"
        :col="item"
        :hasCheckbox="false"
        :minWidth="minWidth"
      >
      </custom-column>
    </template>

  </el-table-column>
</template>
<script>
export default {
  name: "CustomColumn",
  props: {
    col: {
      type: Object,
    },
    label: {
      type: String,
    },
    width: {
      type: String,
    },
    minWidth: {
      type: String,
    },
    hasCheckbox: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {};
  },
  methods: {
    checkChange(checked) {
      this.col.checked = false;
      if (this.col.code) this.$emit("checkChange", checked, this.col.code);
    },
    colData(row) {
      let keys = this.col.prop.split(".");
      let data = row[keys[0]];
      keys.forEach((key, index) => {
        if (index == 0) return;
        data = data[key];
      });
      return data;
    },
  },
  mounted() {
    // if (this.col.checked)
    //   this.$emit("checkChange", this.col.checked, this.col.code);
  },
  created() {},
};
</script>
<style lang="less" scoped>
/deep/ .el-checkbox__inner {
  border-radius: 3px;
  border: solid 2px #333333;
  width: 20px;
  height: 20px;
  margin-right: 6px;
  background-color: transparent;
}
/deep/ .is-focus {
  .el-checkbox__inner {
    border-color: #333333;
  }
}

.el-checkbox {
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;

  /deep/ .is-checked > .el-checkbox__inner {
    background: #ff0000;
    border-color: #ff0000;
  }
  /deep/ .is-checked > .el-checkbox__inner::after {
    top: 1px;
    left: 5px;
    border-width: 3px;
  }

  .el-checkbox__label {
    color: #333333;
    font-weight: bold;
    font-size: 16px;
    padding-left: 0px;
  }

  /deep/ .is-indeterminate > .el-checkbox__inner {
    background: #ff0000;
    border-color: #ff0000;
  }
  /deep/ .is-indeterminate > .el-checkbox__inner::before {
    top: 7px;
    left: 0px;
    height: 4px;
    width: 19px;
  }
}

/deep/ .el-checkbox__label {
  color: #333333 !important;
  font-weight: bold;
  font-size: 16px;
  padding-left: 0px;
}

.column-text {
  color: #333333;
  font-weight: bold;
  font-size: 16px;
  white-space: pre-line;
}
</style>