import { registerShape } from "viser-vue";

function getFillAttrs(cfg) {
  var attrs = Util.mix(cfg.style, {
    fill: cfg.color,
    fillOpacity: cfg.opacity
  });
  return attrs;
}

function getRectPath(points) {
  var path = [];
  for (var i = 0; i < points.length; i++) {
    var point = points[i];
    if (point) {
      var action = i === 0 ? "M" : "L";
      path.push([action, point.x, point.y]);
    }
  }
  var first = points[0];
  path.push(["L", first.x, first.y]);
  path.push(["z"]);
  return path;
}
// 顶边带圆角
Shape.registerShape("interval", "top", {
  draw: function draw(cfg, container) {
    const attrs = getFillAttrs(cfg);
    let path = getRectPath(cfg.points);
    path = this.parsePath(path); // 将 0 - 1 的值转换为画布坐标
    const temp = [];
    temp.push(["M", path[0][1], path[0][2] - 10]);
    temp.push(["L", path[1][1], path[1][2] - 10]);
    temp.push(["L", path[2][1], path[2][2] - 10]);
    temp.push(["L", path[3][1], path[3][2] - 10]);
    temp.push(["Z"]);
    return container.addShape("path", {
      attrs: Util.mix(attrs, {
        path: temp
      })
    });
  }
});
// 底边带圆角
Shape.registerShape("interval", "center", {
  draw: function draw(cfg, container) {
    const attrs = getFillAttrs(cfg);
    let path = getRectPath(cfg.points);
