    temp.push(["L", path[2][1], path[2][2] - 5]);
    temp.push(["L", path[3][1], path[3][2] - 5]);
    temp.push(["Z"]);
    return container.addShape("path", {
      attrs: Util.mix(attrs, {
        path: temp
      })
    });
  }
});
