<template>
  <div>
    <v-chart
      :forceFit="width?false:true"
      :height="height"
      :width="width"
      :padding="['auto',40,'auto',100]"
      renderer="svg"
      :scale="scale"
      ref="chart"
      :data="data"
    >
      <v-tooltip :shared="true" :htmlContent="htmlContent" />
      <v-axis data-key="value" :line="axis_line" :grid="null" />
      <v-axis data-key="value1" :line="axis_line" :grid="null" position="left" />
      <v-axis data-key="value2" :line="axis_line" :grid="null" position="right" :z-index="100" />
      <v-axis data-key="value3" :line="axis_line" :grid="null" position="right" />
      <v-axis data-key="dataTime" :line="axis_line" :tickLine="null" />
      <v-legend position="top-center" data-key="value1" :show="false"></v-legend>
      <!--Y轴 右-->
      <v-guide
        type="line"
        :top="true"
        :start="['end','end']"
        :end="['end','start']"
        :lineStyle="axis_line"
      ></v-guide>
      <!--X轴 上-->
      <v-guide
        type="line"
        :top="true"
        :start="['start','end']"
        :end="['end','end']"
        :lineStyle="axis_line"
      ></v-guide>
      <v-line position="dataTime*value" :size="2" color="red"></v-line>
      <v-point position="dataTime*value" :size="3" color="red" shape="circle" :tooltip="false"></v-point>
      <v-line position="dataTime*value1" :size="2" color="blue"></v-line>
      <v-point position="dataTime*value1" :size="3" color="blue" shape="circle" :tooltip="false"></v-point>
      <v-line position="dataTime*value2" :size="2" color="pink"></v-line>
      <v-point position="dataTime*value2" :size="3" color="pink" shape="circle" :tooltip="false"></v-point>
      <v-line position="dataTime*value3" :size="2" color="yellow"></v-line>
      <v-point position="dataTime*value3" :size="3" color="yellow" shape="circle" :tooltip="false"></v-point>
    </v-chart>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
      default: () => [
        {
          dataTime: "2020-08-01",
          key: "脱硫单元能耗",
          value: 2843.36,
          value1: 583.86,
          value2: -3533.23,
          value3: 977.08,
          type: "bar"
        },
        {
          dataTime: "2020-08-02",
          key: "脱水单元能耗",
          value: 14.57,
          value1: 5105.72,
          value2: 579.8,
          value3: -2209.11,
          type: "bar"
        },
        {
          dataTime: "2020-08-03",
          key: "硫磺回收单元能耗",
          value: 418.17,
          value1: 1416.22,
          value2: 4882.65,
          value3: 583.86,
          type: "bar"
        },
        {
          dataTime: "2020-08-04",
          key: "尾气处理单元能耗",
          value: -1241.47,
          value1: 14.59,
          value2: 14.6,
          value3: 5008.27,
          type: "bar"
        }
      ]
    },
    height: {
      type: Number,
      default: 506
    },
    width: {
      type: Number
    }
  },
  computed: {},
  data() {
    function markerCallback(x, y, r) {
      return [
        ["M", x - 35, y - 10],
        ["L", x + 5, y - 10],
        ["L", x + 5, y + 10],
        ["L", x - 35, y + 10],
        ["Z"]
      ];
    }
    return {
      axis_line: {
        stroke: "#8dc9ff",
        lineWidth: 2,
        lineDash: null
      },
      htmlContent: function(title, items) {
        //-1 样式
        var tooltipsttyle = `position: absolute;
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: 3px;
        color: rgb(87, 87, 87);
        font-size: 12px;
        padding: 10px 10px 6px 10px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)`;
        var listStyle = `margin: 0;
        padding: 0px 0;
        list-style-type: none;text-align:left;`;
        var valurStyle = `margin-left: 30px;
        display: inline;
        float: right;`;

        let unit = "kgce/h";
        //-2 构造tooltip
        var li = "";
        for (var i = 0; i < items.length; i++) {
          let item = items[i];
          if (item.name == "value") {
            item.name = "系列单位综合能耗";
            unit = "kg标煤/10⁴Nm³";
          }

          li += `<li data-index={index} style="margin-bottom:10px">
          <svg viewBox="0 0 5 5" class="g2-tooltip-marker" style="width: 5px; height: 5px; border-radius: unset; display: inline-block; margin-right: 8px;"><path d="M2.5,2.5m-2.5,0a2.5,2.5,0,1,0,5,0a2.5,2.5,0,1,0,-5,0" fill="${item.color}" stroke="none"></path></svg>
          </span>${item.name}<span class="g2-tooltip-value" style="${valurStyle}">${item.value} ${unit}</span>
          </li>`;
        }
        var dom = `<div class="g2-tooltip" style="${tooltipsttyle}">
        <div class="g2-tooltip-title" style="margin-bottom: 10px;">${title}</div>
        <ul class="g2-tooltip-list" style="${listStyle}">
        ${li}
        </ul>
        </div>`;
        return dom;
      },

      legendOnClick: (ev, chart) => {
        const item = ev.item;
        const value = item.value;
        const checked = ev.checked;
        const geoms = chart.getAllGeoms();
        console.log(ev, chart, geoms);
        // for (let i = 0; i < geoms.length; i++) {
        //   const geom = geoms[i];
        //   console.log(
        //     geom.getFieldsForLegend(),
        //     geom,
        //     geom.getYScale(),
        //     geom.getYScale().value,
        //     value
        //   );
        //   if (geom.getYScale().field === value) {
        //     if (checked) {
        //       geom.show();
        //     } else {
        //       geom.hide();
        //     }
        //   }
        // }
      },
      scale: [
        {
          dataKey: "dataTime",
          type: "cat"
        },
        {
          dataKey: "value"
        },
        {
          dataKey: "value1"
        },
        {
          dataKey: "value2"
        },
        {
          dataKey: "value3"
        }
      ],
      intervalLabel: [
        "value",
        {
          textStyle: {
            fill: "#fff",
            fontSize: 16
          },
          offset: 15,
          type: "interval",
          formatter: (val, item, i) => {
            return `${val}t标煤\n50%`;
          },
          position: "middle"
        }
      ],

      style: {
        shadowColor: "rgba(153, 153, 153, 0.9)",
        shadowOffsetX: 10,
        shadowOffsetY: -3,
        fillOpacity: 1,
        shadowBlur: 35
      },
      line: {
        lineWidth: 2,
        stroke: "rgb(141,201,255)",
        fill: "rgb(141,201,255)"

        // lineJoin: "round"
      },
      line2: {
        lineWidth: 2,
        stroke: "rgb(141,201,255)",
        fill: "rgb(141,201,255)"
      },

      color: [
        "key",
        ["#92b157", "#88628b", "#7fd0bd", "#6b9e92", "#a4a79d", "#e2e767"]
      ],
      padding: [20, 160, 80, 60],
      label: {
        textStyle: {
          fill: "#333333",
          fontSize: 16,
          fontWeight: 550
        },
        offsetY: 5,
        autoRotate: false
      },
      tooltip: [
        "key*value",
        (key, value) => {
          return {
            name: key,
            value: value
          };
        }
      ],
      adjust: [
        {
          type: "dodge",
          dodgeBy: "dataTime" // 按照 type 字段进行分组
          //  marginRatio: 0 // 分组中各个柱子之间不留空隙
        },
        {
          type: "stack"
        }
      ]
    };
  },
  methods: {}
};
</script>
